<template>
    <div class="foods">
        <div ref='left'>
            <div class="left" >
                <div class="lists" @click="checkFood(i)" v-for="(item,i) in foods" :key="i" :class="{'current' : i === currentIndex}">{{item.name}}</div>
            </div>
        </div>
        <div class="right" ref='right'>
            <div>
                <div class="listsTitle food-list-hook" v-for="(item,i) in foods" :key="i">
                    <div class="title">{{item.name}}</div>
                    <div class="foodsImg" v-for="(food,i) in item.foods" :key="i">
                        <img :src="food.icon" @click="showFood(food)">
                        <div class="foodMsg" v-if="food">
                            <div class="foodname">{{food.name}}</div>
                            <div>{{food.description}}</div>
                            <div>月销{{food.sellCount}}份，好评率{{food.rating}}%</div>
                            <div>¥{{food.price}}</div>
                        </div>
                        <add :food="food"></add>
                    </div>
                </div>
            </div>
        </div>
        
    </div>
</template>


<script>
    import { mapGetters, mapActions } from 'vuex';
    import BScroll from 'better-scroll';
    import add from '../add/add.vue';
    export default {
        data(){
            return {
                listHight: [],
                scrollY: 0,
            }
        },
        components: {
            add
        },
        created() {
            this.$nextTick( ()=>{
                setTimeout( ()=>{
                    this.leftScroll();
                    this.rightScroll();
                    this.calcHeight();
                }, 100);
            })
        },
        computed: {
            ...mapGetters([
                'foods'
            ]),
            // 计算当前滚动索引
            currentIndex(){
                // 循环listHeight数组
                for(let i = 0; i <this.listHight.length; i++ ) {
                    let height1 = this.listHight[i];
                    let height2 = this.listHight[ i + 1 ];
                    // 根据滚动的y轴坐标值，与listHeight数组区间进行比较
                    if(this.scrollY >= height1 && this.scrollY < height2 ) {
                        return i ;
                    }
                }
                return 0 ;
            }
        },
        // 事件
        methods: {
            showFood(food){
                this.$store.dispatch('showFood',food);
            },
            leftScroll(){
                let menuBScroll = new BScroll( this.$refs.left , {
                    click: true
                } );
            },
            rightScroll(){
                this.listBScroll = new BScroll( this.$refs.right , {
                    click: true,
                    probeType: 3
                } );
                this.listBScroll.on('scroll',(pos) => {
                    // console.log(Math.abs( Math.round(pos.y) ));
                    this.scrollY = Math.abs( Math.round(pos.y) ) +2;
                })
            },
            calcHeight() {
                let foodList = this.$refs.right.getElementsByClassName("food-list-hook");
                let height = 0;
                this.listHight.push( height );
                for( let i = 0; i < foodList.length; i ++) {
                    height += foodList[i].clientHeight;
                    this.listHight.push( height );
                }
                // console.log( this.listHight );
            },
            checkFood(i){
                let foodList = this.$refs.right.getElementsByClassName("food-list-hook");
                let el = foodList[i];
                this.listBScroll.scrollToElement(el,300);
            }
        }
    }
</script>


<style scoped lang="less">
    
    .foods{
        width: 100%;
        position: absolute;
        top: 3.75rem;
        bottom: 0.9rem;
        display: flex;
        overflow: hidden;
        .left{
            width: 1.6rem;
            flex: 0 0 1.6rem;
            background: #f3f5f7;
            text-align: center;
            
            .lists{
                border-bottom: 1px solid rgba(7,17,27,0.1);
                height: 1.08rem;
                position: relative;
                box-sizing: border-box;
                padding-top: 0.42rem;
                &.current{
                    color: #FA5555;
                    background: rgba(7,17,27,0.1);
                }
            }
        }
        .right{
            flex: 1;
            .listsTitle{
                .title{
                    border-left: 0.07rem solid rgba(7,17,27,0.1);
                    font-size: 0.28rem;
                    padding-left: 0.2rem;
                    background: rgba(7,17,27,0.1);
                    height: 0.5rem;
                    line-height: 0.5rem;
                }
                .foodsImg{
                    height: 1.83rem;
                    margin-left: 0.4rem;
                    display: flex;
                    border-bottom: 0.01rem solid rgba(7,17,27,0.1);
                    box-sizing: border-box;
                    padding-top: 0.36rem;
                    padding-bottom: 0.36rem;
                    line-height: 0.36rem;
                    img{
                        width: 1.18rem;
                        height: 1.18rem;
                    }
                    .foodMsg{
                        padding-left: 0.2rem;
                        .foodname{
                            color: rgb(7,17,27);
                        }
                    }
                    .foodMsg:nth-child(2){
                        color: #5A5E66;
                    }
                }
            }
        }
    }
    
</style>


