<template>
    <div class="wrap " v-show="showFood" ref="abc">
        <div>
            <div class="top">
                <div class="back" @click="closeFood">
                    <img src="../../../static/images/back.png" alt="">
                </div>
                <div class="foodImg"><img :src="food.image" alt=""></div>
                <div class="foodName">{{food.name}}</div>
                <div class="foodSell">月销{{food.sellCount}}份 &nbsp; 好评率{{food.rating}}%</div>
                <div class="price">¥{{food.price}} <span>¥{{food.price+4}}</span></div>
                <div class="shops">加入购物车</div>

            </div>

            <div class="hr"></div>

            <div class="goodsInfo">
                <div class="goods">商品介绍</div>
                <div class="info">{{food.info}}</div>
            </div>

            <div class="hr"></div>

            <div class="comment">
                <div class="goods">商品评价</div>
                <div class="goodsTitle">
                    <div class="aa">全部</div>
                    <div class="bb">推荐</div>
                    <div class="cc">吐槽</div>
                </div>

                <div class="only">
                    <img src="../../../static/images/dui.png">
                    <span>只看有内容评价</span>
                </div>

                <div class="commentCont" v-for="(item,i) in ratings" :key="i">
                    <div class="time">2017-3-02 12:34</div>
                    <div class="comments">
                        <img src="../../../static/images/yes.png" alt="">
                        <span>{{item.text}}</span>
                    </div>
                    <div class="user">
                        <div >{{item.username}}</div>
                        <img :src="item.avatar" >
                    </div>
                </div>

            </div>
        </div>
    </div>
    </div>
</template>


<script>
import { mapGetters, mapActions } from 'vuex';
import BScroll from 'better-scroll'; 
export default {
    data() {
        return {
            i : 0
        }
    },
    computed: {
        ...mapGetters([
            'showFood',
            'food',
            'ratings'
        ])
    },

    updated() {
        this.$nextTick( ()=>{
            setTimeout( ()=>{
                this.abcBS();
            }, 100);
        })
    },
    methods: {
        closeFood(){
            this.$store.dispatch("closeFood");
        },
        abcBS(){
            let abcBS = new BScroll( this.$refs.abc , {
                click: true,
            } );
        },
        
    }
}
</script>


<style lang="less" scoped>
    .back{
        position: absolute;
        top: 0.2rem;
        left: 0.2rem;
        // width: rem;
        font-size: 0.32rem;
        color: wheat;
        img{
            width: 0.4rem;
        }
        
    }
    .wrap{
        overflow: hidden;
        position: absolute;
        top: 0;bottom: 0;left: 0;right: 0;
        background: #fff;
        .top{
            border-bottom: 0.01rem solid #e6e7e8;
        }
        .foodImg{
            img{
                width: 100%;
                height: 7.5rem;
            }
        }
        .foodName{
            font-size: 0.4rem;
            margin:0.36rem 0 0.16rem 0.36rem;
        }
        .foodSell{
            font-size: 0.28rem;
            margin: 0 0 0.36rem 0.36rem;
            color: gray;
        }
        .price{
            color: red;
            font-size: 0.4rem;
            margin: 0 0 0 0.36rem;
            span{
                color: gray;
                font-size: 0.28rem;
            }
        }
        .shops{
            background: #00a0dc;
            width: 1.68rem;
            height: 0.68rem;
            border-radius: 0.36rem;
            text-align: center;
            line-height: 0.68rem;
            color: #fff;
            position: relative;
            left: 5.2rem;
            top: -1rem;

        }
        .hr{
            height: 0.32rem;
            background: #f3f5f7;
        }
        .goodsInfo{
            border-bottom: 0.01rem solid #e6e7e8;
            border-top: 0.01rem solid #e6e7e8;
            .goods{
                font-size: 0.4rem;
                margin: 0.36rem 0 0.12rem 0.36rem;
                color: #464a4f;
            }
            .info{
                font-size: 0.30rem;
                line-height: 0.5rem;
                color: gray;
                margin-left: 0.52rem;
                margin-right: 0.52rem;
                margin-bottom: 0.36rem;
            }
        }

        .comment{
            border-top: 0.01rem solid #e6e7e8;
            .goods{
                font-size: 0.4rem;
                margin: 0.36rem 0 0.12rem 0.36rem;
                color: #464a4f;
            }
            .goodsTitle{
                display: flex;
                font-size: 0.32rem;
                margin: 0 0.36rem 0.36rem 0.36rem;
                border-bottom: 1px solid #e6e7e8;
                padding-bottom: 0.36rem;
                .aa,.bb,.cc{
                    width: 1.2rem;
                    height: 0.65rem;
                    margin-right: 0.16rem;
                    text-align: center;
                    line-height: 0.65rem;
                }
                .aa{
                    color: #fff;
                    background: #00a0dc;
                }
                .bb{
                    color: #464a4f;
                    background: #ccecf8;
                }
                .cc{
                    color: #464a4f;                    
                    background: #e9ebec;
                }
            }
            .only{
                color: #93999f;
                font-size: 0.32rem;
                margin: 0.24rem 0.36rem 0.24rem 0.36rem;
                border-bottom: 0.01rem solid #e6e7e8;
                padding-bottom: 0.24rem;
                img{
                    width: 0.42rem;
                }
                span{
                    position: relative;
                    top: -0.1rem #e6e7e8;
                }
            }
        }
        .commentCont{
            margin: 0 0.36rem 0.32rem 0.36rem;
            border-bottom: 0.01rem solid #e6e7e8;
            .time{
                color: #93999f;
                font-size: 0.28rem;
                margin-bottom: 0.12rem;
            }
            .comments{
                img{
                    width: 0.4rem;
                }
                span{
                    display: inline-block;
                    width: 4rem;
                    line-height: 0.28rem;
                    position: relative;
                }
            }
            .user{
                position: relative;
                top: -0.55rem;
                left: 4rem;
                bottom: 0rem;
                display: flex;
                div{
                    width: 2rem;
                    text-align: center;
                    position: relative;
                    color: #93999f;
                    padding-top: 0.07rem;
                }
                img{
                    width: 0.4rem;
                    height: 0.4rem;
                }
            }
        }
    }
</style>



