// 导入types文件
import types from "../types.js";
import axios from "axios";

var state = {
    seller:{},
    detailShow: false,
}

var getters = {
    seller( state ) {
        return state.seller;
    },
    detailShow( state ){
        return state.detailShow;
    }
}

var actions = {
    // 索取数据
    getSeller( { commit,state } ){
        axios.get('./data.json').then( (res) => {
            // console.log(res.data)
            // if( res.data.errno === 0 ) {
                commit( types.GET_SELLER , res.data.seller );
            // }
        });
    },
    // 点击显示窗口
    showDetail( { commit } ) {
        commit( types.showDetail );
    },
    hideDetail( { commit } ) {
        commit( types.hideDetail );
    }
}
// 展示 执行
var mutations = {
    [types.GET_SELLER]( state, data ) {
        state.seller = data;
    },
    [types.showDetail]( state ){
        // console.log(state)
        state.detailShow = true;
    },
    [types.hideDetail]( state ){
        state.detailShow = false;
    }
}

// 导出
export default {
    state,
    getters,
    actions,
    mutations
}


