
<template >
    <transition enter-active-class="animated zoomIn" leave-active-class="animated zoomOut">
        <div class="wrap" v-show="detailShow" @click='hideDetail'>
            <div class="bgWrap"  >
                <div class="shopName">{{seller.name}}</div>
                <star :score = 'seller.score' :size = "'big'" class="star"></star>
                <div class="msgTitle">
                    <span>~~~~~~~~~~</span>
                     优惠信息
                    <span>~~~~~~~~~~</span>
                </div>
                <div v-for="(item,i) in seller.supports" :key="i" class="sellList">
                    <img src="../../../static/images/decrease.png" v-if="item.type == 0">
                    <img src="../../../static/images/discount.png" v-if="item.type == 1">
                    <img src="../../../static/images/guarantee.png" v-if="item.type == 4">
                    <img src="../../../static/images/invoice.png" v-if="item.type == 3">
                    <img src="../../../static/images/special.png" v-if="item.type == 2">
                    <span>{{item.description}}</span>
                </div>
                <div class="msgTitle">
                    <span>~~~~~~~~~~</span>
                     商家公告
                    <span>~~~~~~~~~~</span>
                </div>
                <div class="content">{{seller.bulletin}}</div>
                <div class="close"><img src="../../../static/images/close.png"></div>
            </div>
        </div>
    </transition>
</template>





<script>
    import { mapGetters } from 'vuex';
    import star from '../star/star.vue';
    
    export default {
        props:['name'],
        components: {
            star
        },
        computed: {
            ...mapGetters([
                'seller',
                'detailShow',
            ])
        },
        methods: {
            hideDetail(){
				this.$store.dispatch('hideDetail');
			}
        }
    }
</script>




<style scoped lang="less">
    .wrap{
        position: absolute;
        top: 0.5rem;left: 0.5rem;right: 0.5rem;bottom: 0.5rem;
        background: rgba(00, 00, 00, 0.8);
        color: #fff;
    }
    .bgWrap{
        text-align: center; 
        .shopName{
            font-size: 0.32rem;
            font-weight:700;
            color: rgb(255,255,255);
            margin-top: 1.28rem;
        }
    }
    .star{
        margin-top: 0.32rem;
    }
    .msgTitle{
        margin-top: 0.4rem;
        margin-bottom: 0.48rem;
        font-size: 0.32rem;
    }
    .sellList{
        text-align: left;
        margin-left: 1rem;
        line-height: 0.6rem;
        img{
            width: 0.33rem;
            height: 0.33rem;
            
        }
    }
    .content{
        width: 70%;
        text-align: left;
        line-height: 0.4rem;
        margin-left: 1rem;
    }
    .close{
        margin-top: 0.7rem;
        // position: absolute;
        z-index: 999;
        img{
            width: 0.63rem;
        }
    }
    
</style>



