<template>
    <div class="wrap" v-show="shoppingShow">
        <div class="mohu"></div>
        <div class="shopping">
            <div class="cart"><span class="aa">购物车</span><span class="bb" @click="clear">清空</span></div>
            <div class="foods" v-for="(item,i) in selcetFoods" :key="i">
                <div class="foodName">{{item.name}}</div>
                <div class="price">¥ {{item.price}} </div>
                <div class="add"><add :food = 'item'></add></div>
            </div>
            <div>&nbsp;</div>
        </div>
    </div>
</template>



<script>
import { mapGetters } from 'vuex';
import add from '../add/add'
export default {
    components:{
        add
    },
    props:['foods'],
    computed: {
        ...mapGetters([
            'shoppingShow'
        ]),
        selcetFoods(){
            let  foodss = [];
            this.foods.forEach( good => {
                good.foods.forEach( food => {
                    if( food.count > 0 ){
                        foodss.push( food );
                    }
                })
            });
            return foodss;
        },
        // 计算购物车商品的总数量
        totalCount(){
            let count = 0 ;
            this.selcetFoods.forEach( food => {
                count += food.count;
            });
            return count;
        }
    },
    methods:{
        // 清空购物车
        clear(){
            this.foods.forEach( good => {
                good.foods.forEach( food => {
                    food.count = 0;
                })
            })
        }
    }
}
</script>



<style lang="less" scoped>
    .wrap{
        position: absolute;
        top: 0;left: 0;right: 0;bottom: 0;
        width: 100%;
        height: 11.4rem;
        .mohu{
            background: rgba(7,17,27,0.6);
            height: 12.2rem;
        }
        .shopping{
            width: 100%;
            background: #fff;
            position: absolute;
            bottom: -1rem;
            .cart{
                height: 1rem;
                display: inline;
                display: flex;
                justify-content: space-between;
                line-height: 1rem;
                background: #f3f5f7;
                border-bottom: 0.01rem solid rgba(7,17,27,0.1);
                span{
                    display: inline-block;
                }
                .aa{
                    font-size: 0.36rem;
                    color: rgb(7, 17, 27);
                    padding-left: 0.36rem;
                }
                .bb{
                    font-size: 0.32rem;
                    color: rgb(0,160,220);
                    padding-right: 0.36rem;
                }
            }
            .foods{
                font-size: 0.36rem;
                color: rgb(7,17,27);
                line-height: 0.96rem;
                padding-left: 0.36rem;
                border-bottom: 0.01rem solid rgba(7,17,27,0.1);
                display: flex;
                flex-direction: row;
                .foodName{
                    width: 4rem;
                }
                .price{
                    color: tomato;
                    width: 1.2rem;
                }
            }
        }
    }
    
</style>



