<!-- html内容 -->
<template>
    <div class="myDiv">
        <!-- 背景搭建 -->
        <div class="bWrap">
            <div class="wrap">
                <img :src="seller.avatar">
            </div>
            <div class="wrapBgimg"></div>
        </div>
        <!-- header里面的内容 -->
        <div class="content" >
            <!-- 商家店名、优惠 -->
            <div class="contentBox">
                <div class="boxLeft"><img :src="seller.avatar"></div>
                <div class="boxRight">
                    <div class="logo"><img src="/static/images/brand.png"><span class="shopName">{{seller.name}}</span></div>
                    <div class="courier">{{seller.description}}/{{seller.deliveryTime}}分钟送达</div>
                    <div class="sell" v-if="seller.supports">
                        <img src="../../../static/images/decrease.png" v-if="seller.supports[i].type == 0">
                        <img src="../../../static/images/discount.png" v-if="seller.supports[i].type == 1">
                        <img src="../../../static/images/guarantee.png" v-if="seller.supports[i].type == 4">
                        <img src="../../../static/images/invoice.png" v-if="seller.supports[i].type == 3">
                        <img src="../../../static/images/special.png" v-if="seller.supports[i].type == 2">
                        <span v-if="seller.supports[i]">{{seller.supports[i].description}}</span>
                    </div>
            </div>
            <!-- 公告栏 -->
            <div class="banner">
                <img src="/static/images/bulletin.png">
                <span>{{seller.bulletin}}</span>
            </div>
            <div class="sellList" v-if="seller.supports" @click="showDetail"> {{seller.supports.length}}个 ></div>
        </div>
    </div> 
    </div>   
</template>


<!-- js代码 -->
<script>
    // 导入 方法
    import { mapGetters, mapActions} from 'vuex';
    export default {
        data:function(){
            return {
                i:0
            }
        },
        updated(){
            setTimeout( ()=>{
                if(this.i == this.$store.getters.seller.supports.length-1){
                    this.i = 0;
                }
            },3000)
        },
        computed: {
            ...mapGetters([
                'seller'
            ]),
        },
        methods: {
            ...mapActions([
                'showDetail'
            ])
        }
    
}
</script>


<!-- css样式 -->
<style scoped lang="less">
    .myDiv{
        height: 2.68rem;
    }
    .bWrap{
        width: 100%;
        height: 2.68rem;
        position: relative;
        .wrap{
            width: 100%;
            height: 2.68rem;
            text-align: center;
            img{
                width: 80%;
                height: 100%;
                // 写一个适配
                -webkit-filter: blur(0.2rem); /* Chrome, Opera */
                -moz-filter: blur(0.2rem);
                -ms-filter: blur(0.2rem);    
                filter: blur(0.2rem);
            }        
        }
        .wrapBgimg{
            width: 100%;
            height: 2.68rem;
            background: black;
            opacity: 0.3;
            position: absolute;
            top: 0;
        }
    }
    // 内容
    .content{
        position: relative;
        top: -2.68rem;
        width: 100%;
        height: 2.68rem;
        .boxLeft img{
            width: 1.28rem;
            height: 1.28rem;
            margin-top: 0.48rem;
            margin-left: 0.48rem;
            border-radius: 0.05rem;
        }
        .contentBox{
            display: flex;
            .boxRight{
                margin-top: 0.52rem;
                margin-left: 0.52rem;
                .logo {
                    text-align: center;
                    height: 0.35rem;
                    img{
                        width: 0.63rem;
                        height: 0.35rem;
                        border-radius: 0.05rem;
                    }
                    span{
                        display: inline-block;
                        position: relative;
                        top: -0.03rem;
                        font-size: 0.32rem;
                        color: #fff;
                        line-height: 0.35rem;
                        margin-left: 0.12rem;
                        overflow: hidden;
                    }
                }
                .courier{
                    margin-top: 0.16rem;
                    font-size: 0.26rem;
                    color: #fff;
                }
                .sell{
                    margin-top: 0.2rem;
                    img{
                        width: 0.3rem;
                        border-radius: 0.05rem;
                    }
                    span{
                        font-size: 0.25rem;
                        display: inline-block;
                        position: relative;
                        top: -0.05rem;
                        color: #fff;
                        margin-left: 0.08rem;
                        width: 2rem;
                    }
                }
            }  
        }
        .banner{
            display: flex;
            width: 100%;
            height: 0.56rem;
            background: black;
            opacity: 0.4;
            position: absolute;
            bottom: 0;
            line-height: 0.56rem;
            img{
                margin-left: 0.24rem;
                width: 0.47rem;
                height: 0.27rem;
                position: relative;
                top: 0.13rem;
            }
            span{
                color: #fff;
                margin-left: 0.08rem;
                // 文章 太多 ...
                overflow: hidden;
                text-overflow: ellipsis;
                white-space: nowrap;
            }
        }
        .sellList{
            position: absolute;
            right: 0.24rem;
            bottom: 0.78rem;
            width: 1rem;
            height: 0.5rem;
            background: black;
            opacity: 0.5;
            border-radius: 0.5rem;
            color: #fff;
            text-align: center;
            line-height: 0.5rem;
        }
    }
</style>



