<template>
<div class="cc" ref="abcd">
    <div>
        <div class="box">
            <div class="left">
                <div class="scroll">{{seller.foodScore}}</div>
                <div class="scrollTitle">综合评分</div>
                <div class="aside">高于周边商家{{seller.rankRate}}%</div>
            </div>
            <div class="right">
                <div class="stars">
                    <div class="starTitle">服务态度</div>
                    <div class="star"><star :score = 'seller.score' :size = "'big'"></star></div>
                    <div class="scroll">{{seller.foodScore}}</div>
                </div>
                <div class="stars">
                    <div class="starTitle">服务态度</div>
                    <div class="star"><star :score = 'seller.score'></star></div>
                    <div class="scroll">{{seller.serviceScore}}</div>
                </div>
                <div class="time">
                    <div>送达时间 <span>{{seller.deliveryTime}}分钟</span></div>
                </div>
            </div>
        </div>
        <div class="hr"></div>

        <div class="comment">
            <div class="goods">商品评价</div>
            <div class="goodsTitle">
                <div class="aa">全部</div>
                <div class="bb">推荐</div>
                <div class="cc">吐槽</div>
            </div>

            <div class="only">
                <img src="../../../static/images/dui.png">
                <span>只看有内容评价</span>
            </div>

            <div class="commentCont" v-for="(item,i) in ratings" :key="i">
                <div class="time">2017-3-02 12:34</div>
                <div class="comments">
                    <img src="../../../static/images/yes.png" alt="">
                    <span>{{item.text}}</span>
                </div>
                <div class="user">
                    <div >{{item.username}}</div>
                    <img :src="item.avatar" >
                </div>
            </div>

        </div>
    </div>
</div>   
</template>


<script>
import { mapGetters, mapActions } from 'vuex';
import BScroll from 'better-scroll'; 
import star from '../star/star';
export default {
    components:{
        star
    },
    computed: {
        ...mapGetters([
            'ratings',
            'seller'
        ])
    },
    methods: {
        abcBS(){
            let abcBS = new BScroll( this.$refs.abcd , {
                click: true,
            } );
        },
        
    },
    created() {
        this.$nextTick( ()=>{
            setTimeout( ()=>{
                this.abcBS();
            }, 500);
        })
    },
}
</script>


<style scoped lang="less">
.cc{
    overflow: hidden;
    bottom: 0.9rem;
    height: 8.8rem;
    .box{
        border-bottom: 0.01rem solid rgba(07, 17, 27,0.1);
        width: 100%;
        display: flex;
        .left{
            margin-left: 0.25rem;
            width: 2.75rem;
            height: 2.1rem;
            border-right: 0.01rem solid rgba(07, 17, 27,0.1);
            text-align: center;
            padding-top: 0.3rem;
            line-height: 0.55rem;
            box-sizing: border-box;
            .scroll{
                font-size: 0.5rem;
                color: orange;
            }
            .scrollTitle{
                font-size: 0.28rem;
            }
            .aside{
                color: gray;
            }
        }
        .right{
            box-sizing: border-box;
            padding-left: 0.28rem;
            margin-top: 0.1rem;
        }
        .stars{
            padding-top: 0.09rem;
            width: 4rem;
            display: flex;
            line-height: 0.55rem;
            .starTitle{
                font-size: 0.28rem;
                color: rgb(07, 17, 27);
            }
            .star{
                margin: 0 0 0 0.05rem;
            }
            .scroll{
                font-size: 0.3rem;
                color: orange;
            }
        }
        .time{
            font-size: 0.28rem;
            padding-top: 0.15rem;
            span{
                color: gray;
                
            }
        }
    }
    .hr{
        height: 0.32rem;
        background: #f3f5f7;
    }

    // 评论
            .comment{
            border-top: 0.01rem solid #e6e7e8;
            .goods{
                font-size: 0.4rem;
                margin: 0.36rem 0 0.12rem 0.36rem;
                color: #464a4f;
            }
            .goodsTitle{
                display: flex;
                font-size: 0.32rem;
                margin: 0 0.36rem 0.36rem 0.36rem;
                border-bottom: 1px solid #e6e7e8;
                padding-bottom: 0.36rem;
                .aa,.bb,.cc{
                    width: 1.2rem;
                    height: 0.65rem;
                    margin-right: 0.16rem;
                    text-align: center;
                    line-height: 0.65rem;
                }
                .aa{
                    color: #fff;
                    background: #00a0dc;
                }
                .bb{
                    color: #464a4f;
                    background: #ccecf8;
                }
                .cc{
                    color: #464a4f;                    
                    background: #e9ebec;
                }
            }
            .only{
                color: #93999f;
                font-size: 0.32rem;
                margin: 0.24rem 0.36rem 0.24rem 0.36rem;
                border-bottom: 0.01rem solid #e6e7e8;
                padding-bottom: 0.24rem;
                img{
                    width: 0.42rem;
                }
                span{
                    position: relative;
                    top: -0.1rem #e6e7e8;
                }
            }
        }
        .commentCont{
            margin: 0 0.36rem 0.32rem 0.36rem;
            border-bottom: 0.01rem solid #e6e7e8;
            .time{
                color: #93999f;
                font-size: 0.28rem;
                margin-bottom: 0.12rem;
            }
            .comments{
                img{
                    width: 0.4rem;
                }
                span{
                    display: inline-block;
                    width: 4rem;
                    line-height: 0.28rem;
                    position: relative;
                }
            }
            .user{
                position: relative;
                top: -0.55rem;
                left: 4rem;
                bottom: 0rem;
                display: flex;
                div{
                    width: 2rem;
                    text-align: center;
                    position: relative;
                    color: #93999f;
                    padding-top: 0.07rem;
                }
                img{
                    width: 0.4rem;
                    height: 0.4rem;
                }
            }
        }
    }
</style>


