<template>
  <div class="wrap">
      <div class="footer" >
        <div class="circle" @click="showShopping">
            <div class="letterCircle" :class="{'current' : totalCount}">
                <img src="../../../static/images/shop.png" alt="">
                <div class="total" v-if="totalCount">{{totalCount}}</div>
            </div>
        </div>
        
        <div class="price" >
            <div class="allPrice" @click="showShopping">&nbsp;¥{{totalPrice}}</div>
            <div class="addPrice" @click="showShopping">另需配送费¥4元</div>
            <div class="startPrice" :class="(totalPrice > seller.minPrice-1) ? current :currents">{{pay}}</div>
        </div>
      </div>
  </div>
</template>

<script>
    import { mapGetters, mapActions} from 'vuex';
    export default {
        data(){
            return{
                current:'current',
                currents:'currents',

            }
        },
        props:['foods'],
        methods:{
            ...mapActions([
                'showShopping'
            ])
        },
		computed:{
            ...mapGetters([
                'seller'
            ]),
            selcetFoods(){
                let  foods = [];
                this.foods.forEach( (good) => {
                    good.foods.forEach( (food) => {
                        if( food.count > 0 ){
                            foods.push( food );
                        }
                    })
            });
            return foods;
			},
            // 计算购物车商品的总数量
            totalCount(){
                let count = 0 ;
                this.selcetFoods.forEach( food => {
                    count += food.count;
                });
                return count;
            },
			// 计算购物车商品总价
			totalPrice(){
				let price = 0;
				this.selcetFoods.forEach( (good) => {
					price += good.count * good.price;
				});
				return price;
            },
			// //结算描述
			pay(){
				if( this.totalPrice === 0 ){
					return ` ¥${this.seller.minPrice}元起送`;
				}else if ( this.totalPrice < this.seller.minPrice ) {
					return `还差¥${ this.seller.minPrice - this.totalPrice }元起送`
				}else{
					return `去结算`;
				}
			}
        }



    }
</script>


<style scoped lang="less">
    .footer{
        width: 100%;
        background: #202c3e;
        height: 1rem;
        position: fixed;
        bottom: 0;
        z-index: 0;
        .circle{
            background: #202c3e;
            width: 1.2rem;
            height: 1.2rem;
            border-radius: 50%;
            position: absolute;
            left: 0.4rem;
            top: -0.3rem;
            .letterCircle{
                background: rgb(218, 216, 216);
                width: 0.9rem;
                height: 0.9rem;
                border-radius: 50%;
                position: absolute;
                left: 0.15rem;
                top: 0.2rem;
                &.current{
                    background: #EB9E05;
                }
                
                img{
                    width: 0.6rem;
                    height: 0.5rem;
                    position: absolute;
                    left: 0.15rem;
                    top: 0.19rem;
                }
                .total{
                    background: orangered;
                    width: 0.5rem;
                    height: 0.5rem;
                    border-radius: 50%;
                    text-align: center;
                    line-height: 0.5rem;
                    color:  rgb(218, 216, 216);
                    font-size: 0.3rem;
                    position: relative;
                    left: 0.6rem;
                    top: -0.2rem;
                }
            }
         }
        .price{
            display: flex;
            position: relative;
            left: 1.8rem;
            height: 1rem;
            color: #fff;
            line-height: 0.4rem;
            .allPrice{
                font-size: 0.36rem;
                border-right: 0.01rem solid #333f4a;
                margin-top: 0.2rem; 
                margin-bottom: 0.2rem;
                padding-top: 0.1rem;
                line-height: 0.4rem;
                width: 2.6rem;
            }
            .addPrice{
                width: 4rem;
                padding-left: 0.2rem;
                padding-top: 0.28rem;
            }
            .startPrice{
                font-size: 0.28rem;
                background: #333f4a;
                width: 100%;
                padding-left: 0.2rem;
                padding-top: 0.3rem;
                height: 1rem;
                &.current{
                    background: #EB9E05;
                    margin-left: 0.5rem;
                    padding-left: 0.6rem;
                }
            }
        }


        }
 
        
    
</style>



