// 导入types文件
import types from '../types.js';
import axios from 'axios';

var state = {
    foods: [],
    showFood: false,
    food: {},
    ratings: [],
    shoppingShow: false
}

var getters = {
    foods( state ) {
        return state.foods;
    },
    showFood( state ) {
        return state.showFood;
    },
    food( state ) {
        return state.food;
    },
    ratings( state ) {
        return state.ratings;
    },
    shoppingShow( state ) {
        return state.shoppingShow;
    }
}

var actions = {
    getFoods( { commit, state } ) {
        axios.get('./data.json').then( (res)=> {
            // if( res.data.errno === 0 ) {
                commit( types.GET_FOODS, res.data.goods )
            // }
        });
    },
    showFood( { commit } ,food) {
        // console.log(1);
        commit( types.showFood ,food );
    },
    closeFood( {commit} ) {
        commit( types.closeFood );
    },
    getRatings( { commit, state } ) {
        axios.get('./data.json').then( (res)=> {
            console.log(res.data.ratings);
            // if( res.data.errno === 0 ) {
                commit( types.GET_RATINGS, res.data.ratings );
            // }
        });
    },
    showShopping( {commit} ) {
        commit( types.showShopping );
    }
}

var mutations = {
    [types.GET_FOODS]( state, data){
        // console.log(data);
        state.foods = data;
    },
    [types.showFood]( state ,food) {
        // console.log(1);
        state.showFood = true;
        state.food = food;
    },
    [types.closeFood]( state ) {
        state.showFood = false;
    },
    [types.GET_RATINGS]( state, data ) {
        state.ratings = data;
    },
    [types.showShopping]( state ) {
        if(state.shoppingShow == false){
            state.shoppingShow = true;
        }else{
            state.shoppingShow = false;
        }
    }
}

// 导出
export default {
    state,
    getters,
    actions,
    mutations
}


