
<template>
    <div>
        <div class="icon">
            <div class="jian" @click="jian"><img src="../../../static/images/jian.png" v-show="food.count"></div>
            <div class="num"><span v-if="food.count">{{food.count}}</span></div>
            <div class="jia" @click="add"><img src="../../../static/images/jia.png"></div>
        </div>
    </div>
</template>


<script>
import Vue from 'vue';
export default {
    props:['food'],
    methods:{
        add(){
            if( !this.food.count ){
                Vue.set( this.food , 'count' , 1);
            }else{
                this.food.count ++;
            }
        },
        jian(){
            if( this.food.count ){
                this.food.count --;
            }
        }
    }
}
</script>


<style scoped lang="less">

    .icon{
        display: flex;
        position: absolute;
        right: 0.3rem;
        .jian img{
            width: 0.4rem;
            height: 0.4rem;
            // display: none;
        }
        .jia img{
            width: 0.4rem;
            height: 0.4rem;
        }
        .num{
                // display: none;
            span{
                padding: 0 0.2rem 0 0.2rem;
            }
        }
    }
</style>



