<template>
    <div class="wrap" ref="abc">
        <div class="content">
            <div class="top">
                <div class="shopName">{{seller.name}}</div>
                <div class="stars">
                    <div class="star"><star :score='seller.score'></star></div>
                    <div class="starText">({{seller.ratingCount}})   月售{{seller.sellCount}}单</div>
                    <div class="mark">
                        <img src="../../../static/images/xin.png" alt="">
                        <div>已收藏</div>
                    </div>
                </div>

                <div class="msg">
                    <div class="text1">
                        <div class="msg1">起送价</div>
                        <div class="price1">{{seller.minPrice}}<span>元</span> </div>
                    </div>
                    <div class="text2">
                        <div class="msg2">商家配送</div>
                        <div class="price2">{{seller.deliveryPrice}}<span>元</span> </div>
                    </div>
                    <div class="text3">
                        <div class="msg3">平均配送时间</div>
                        <div class="price3">{{seller.deliveryTime}}<span>元</span> </div>
                    </div>
                </div>
            </div>

            <div class="hr"></div>
            <div class="banner">
                <div class="title">公告与活动</div>
                <div class="text">{{seller.bulletin}}</div>
                <div v-for="(item,i) in seller.supports" :key="i" class="sellList">
                    <img src="../../../static/images/decrease.png" v-if="item.type == 0">
                    <img src="../../../static/images/discount.png" v-if="item.type == 1">
                    <img src="../../../static/images/guarantee.png" v-if="item.type == 2">
                    <img src="../../../static/images/invoice.png" v-if="item.type == 3">
                    <img src="../../../static/images/special.png" v-if="item.type == 4">
                    <img src="../../../static/images/special.png" v-if="item.type == 5">
                    <img src="../../../static/images/special.png" v-if="item.type == 6">
                    <img src="../../../static/images/special.png" v-if="item.type == 7">
                    <span>{{item.description}}</span>
                </div>
            </div>

            <div class="hr"></div>
            <div class="sellerImg">
                <div class="title">商家实景</div>
                <div class="Imgs">
                    <img v-for="(item,i) in seller.pics" :key="i" :src="item" >
                </div>
            </div>

            <div class="hr"></div>
            <div class="sellerMsg" >
                <div class="title">商家信息</div>
                <div class="bill" v-for="(item,i) in seller.infos" :key="i">{{item}}</div>
            </div>
        </div>
    </div>
</template>


<script>
    import { mapGetters,mapActions } from 'vuex';
    import BScroll from 'better-scroll'; 
    import star from '../star/star.vue';
    export default {
        components:{
            star
        },
        created() {
            this.$nextTick( ()=>{
                setTimeout( ()=>{
                    this.abcBS();
                }, 100);
            })
        },
        computed:{
            ...mapGetters([
                'seller'
            ])
        },
        methods:{
            abcBS(){
                let menuBScroll = new BScroll( this.$refs.abc , {
                    click: true
                } );
            },
        }
    }
</script>


<style scoped lang="less">
    .wrap{
        overflow: hidden;
        position: fixed;
        top: 3.8rem;
        bottom: 0.9rem;
        .content{
            .top{
                margin:0.36rem 0.36rem 0.16rem 0.36rem;
            .shopName{
                font-size: 0.4rem;
                color: #5A5E66;
            }
            .stars{
                margin: 0.16rem 0 0 0;
                display: flex;
                border-bottom: 0.01rem solid rgba(7,17,27,0.1);
                .starText{
                    font-size: 0.3rem;
                    margin-left: 0.16rem;
                    margin-top: 0.05rem;
                    color: rgb(77,85,93);
                }
                .mark{
                    position: relative;
                    left: 1.5rem;
                    top: -0.5rem;
                    img{
                        width:0.7rem;
                    }
                    div{
                        font-size: 0.2rem;
                    }
                }
            }
            }
            .msg{
            display: flex;
            justify-content: space-around;
            margin-left: -0.2rem;
            line-height: 0.6rem;
            margin-bottom: 0.2rem;
            margin-top: 0.2rem;
            .text1{
                border-right: 0.01rem solid rgba(7,17,27,0.1);
            }
            .text2{
                border-right: 0.01rem solid rgba(7,17,27,0.1);
            }
            div{
                width: 2.3rem;
                text-align: center;
            }
            .msg1,.msg2,.msg3{
                color: rgb(147,153,159);
                font-size: 0.28rem;
            }
            .price1,.price2,.price3{
                color: rgb(7,17,27);
                font-size: 0.5rem;
                color: rgb(7,17,27);
                span{
                    color: rgb(147,153,159);
                    font-size: 0.28rem;
                }
            }
            .text1{
                
            }
        }
        .banner{
            margin: 0.36rem 0.36rem 0 0.36rem;
            
            .title{
                font-size: 0.4rem;
                margin-bottom: 0.16rem;
            }
            .text{
                line-height: 0.5rem;
                font-size: 0.30rem;
                color: #FA5555;
                padding-bottom:0.32rem;
                border-bottom: 0.01rem solid rgba(7,17,27,0.1);
            }
            .sellList{
                margin-top: 0.32rem;
                padding-bottom: 0.22rem;
                line-height: 0.4rem;
                border-bottom: 0.01rem solid rgba(7,17,27,0.1);
                img{
                    width: 0.5rem;
                }
                span{
                    font-size: 0.3rem;
                    color: #5A5E66;
                    position: relative;
                    bottom: 0.15rem;
                    
                }
            }
        }
        .sellerImg{
            // margin-bottom:5rem;
            margin: 0.36rem 0.36rem 0.36rem 0.36rem;
            .title{
                font-size: 0.4rem;
                margin-bottom: 0.24rem;
            }
            .Imgs{
                
                // border: 0.01rem solid red;
                // overflow: hidden;
                // margin-right: 0.36rem;
                // display: flex;
                overflow: hidden;
                white-space: nowrap;
                img{

                    // float: left;
                    // overflow: hidden;
                    // width: 3rem;
                    // height:2.8rem;
                    // margin-right: 0.2rem;
                }
            }
        }
        .sellerMsg{
            height: 5.5rem;
            margin: 0.36rem 0.36rem 0.36rem 0.36rem;
            .title{
                font-size: 0.4rem;
                padding-bottom: 0.24rem;
                border-bottom: 0.01rem solid rgba(7,17,27,0.1);
            }
            .bill{
                border-bottom: 0.01rem solid rgba(7,17,27,0.1);
                margin: 0.32rem 0 0.32rem 0;
                padding: 0 0 0.32rem 0.24rem;
                font-size: 0.3rem;
                color: #5A5E66
            }
        }
        }
        
        
        .hr{
            height: 0.32rem;
            background: #f3f5f7;
        }
    }

</style>




